import { Component, Element, Event, EventEmitter, h, Listen, Prop } from '@stencil/core';

@Component({
  tag: 'mdx-tab-element',
  styleUrl: 'tab-element.scss',
  shadow: true,
})
export class TabElement {
  @Event()
  tabClick: EventEmitter<{ tab: string; active: boolean }>;

  @Prop({ reflect: true })
  tab!: string;

  @Prop()
  label!: string;

  @Prop({ reflect: true })
  disabled = false;

  @Prop({ reflect: true })
  active = false;

  @Element()
  host: HTMLMdxTabElementElement;

  @Listen('click', { target: 'window' })
  handleOutsideClick(event: MouseEvent) {
    const targetElement = event.target as HTMLElement;

    if (this.active && targetElement.tagName === 'MDX-TAB-ELEMENT' && targetElement !== this.host) {
      this.active = false;
    }
  }

  render() {
    return (
      <button
        class="tab-element"
        disabled={this.disabled}
        onClick={() => {
          if (this.disabled) {
            return;
          }
          this.active = !this.active;
          this.tabClick.emit({ tab: this.tab, active: this.active });
        }}
      >
        <slot name="icon-left" />
        <span class="tab-element__label">{this.label}</span>
      </button>
    );
  }
}
