:host .tab-element {
  box-sizing: border-box;
  width: 100%;
  border-width: 0;
  border-style: solid;
  cursor: pointer;
  user-select: none;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font: var(--mdx-comp-tabs-font-label-default);
  padding: var(--mdx-comp-tabs-padding-tab-element-vertical-default)
    var(--mdx-comp-tabs-padding-tab-element-horizontal-default);
  background-color: var(--mdx-comp-tabs-background-color-default);
  color: var(--mdx-comp-tabs-label-color-default);
  gap: var(--mdx-comp-tabs-gap-icon-label);
  border-color: transparent;
  border-bottom-width: var(--mdx-comp-tabs-border-width-bottom-hover);
  border-top-width: var(--mdx-comp-tabs-border-width-top-hover);
  border-left-width: var(--mdx-comp-tabs-border-width-left-hover);
  border-right-width: var(--mdx-comp-tabs-border-width-right-hover);

  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default) var(--mdx-comp-keyboard-focus-border-radius-default)
      0px 0px;
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: -4px;
  }

  &:hover {
    color: var(--mdx-comp-tabs-label-color-hover);
    border-bottom-width: var(--mdx-comp-tabs-border-width-bottom-hover);
    border-top-width: var(--mdx-comp-tabs-border-width-top-hover);
    border-left-width: var(--mdx-comp-tabs-border-width-left-hover);
    border-right-width: var(--mdx-comp-tabs-border-width-right-hover);
    border-top-left-radius: var(--mdx-comp-tabs-border-radius-hover-top-left);
    border-top-right-radius: var(--mdx-comp-tabs-border-radius-hover-top-right);
    border-bottom-left-radius: var(--mdx-comp-tabs-border-radius-hover-bottom-left);
    border-bottom-right-radius: var(--mdx-comp-tabs-border-radius-hover-bottom-right);
    border-color: var(--mdx-comp-tabs-border-color-hover);
    background-color: var(--mdx-comp-tabs-background-color-hover);
    transition: all var(--mdx-comp-tabs-transition-hover-fade-in-out-duration)
      var(--mdx-comp-tabs-transition-hover-fade-in-out-timing-function)
      var(--mdx-comp-tabs-transition-hover-fade-in-out-delay);
  }

  &:disabled {
    cursor: default;
    color: var(--mdx-comp-tabs-label-color-disabled);
    border-top-width: var(--mdx-comp-tabs-border-width-top-disabled);
    border-left-width: var(--mdx-comp-tabs-border-width-left-disabled);
    border-right-width: var(--mdx-comp-tabs-border-width-right-disabled);
    border-bottom-width: 0;
    border-color: var(--mdx-comp-tabs-border-color-disabled);
    background-color: var(--mdx-comp-tabs-background-color-disabled);
  }

  &__label {
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  ::slotted(*) {
    height: 100%;
    display: flex;
    justify-content: center;
  }
}

:host([active]) .tab-element {
  color: var(--mdx-comp-tabs-label-color-selected);
  border-bottom-width: var(--mdx-comp-tabs-border-width-bottom-selected);
  border-top-width: var(--mdx-comp-tabs-border-width-top-selected);
  border-left-width: var(--mdx-comp-tabs-border-width-left-selected);
  border-right-width: var(--mdx-comp-tabs-border-width-right-selected);
  border-color: var(--mdx-comp-tabs-border-color-selected);
  transition: var(--mdx-comp-tabs-transition-move-selected-border);

  &:disabled {
    color: var(--mdx-comp-tabs-label-color-disabled);
    border-bottom-width: var(--mdx-comp-tabs-border-width-bottom-disabled);
    border-top-width: var(--mdx-comp-tabs-border-width-top-disabled);
    border-left-width: var(--mdx-comp-tabs-border-width-left-disabled);
    border-right-width: var(--mdx-comp-tabs-border-width-right-disabled);
    border-color: var(--mdx-comp-tabs-border-color-disabled);
    background-color: var(--mdx-comp-tabs-background-color-disabled);
  }
}
